<template>
  <el-drawer :title="$t('admin.label-storage-manager')" :visible.sync="visible" :wrapperClosable="false" size="50%">
    <el-form :model="form" :rules="rules" ref="form" style="padding-right: 50px" size="small">
      <el-tabs value="basic" type="card" style="margin-left: 20px">
        <el-tab-pane label="" name="basic">
          <el-form-item label="" label-width="120px">
            <el-radio-group v-model="form.mode" :disabled="editMode">
              <el-radio :label="1"></el-radio>
              <el-radio :label="2"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item prop="name" :label="$t('admin.label-name')" label-width="120px">
            <el-input v-model="form.name" placeholder="" autofocus></el-input>
          </el-form-item>
          <el-form-item prop="provider" label="" label-width="120px">
            <el-select v-model="form.provider" placeholder="" :disabled="editMode" @change="refreshEplist" style="width: 100%">
              <el-option v-for="item in providers" :key="item" :label="item" :value="item"> </el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="bucket" :label="$t('admin.label-bucket')" label-width="120px">
            <el-input v-model="form.bucket" placeholder="" :disabled="editMode"></el-input>
          </el-form-item>
          <el-form-item prop="endpoint" :label="$t('admin.label-endpoint')" label-width="120px">
            <el-autocomplete v-model="form.endpoint" :fetch-suggestions="endpointSearch" @select="endpointSelect" placeholder="" style="width: 100%"></el-autocomplete>
          </el-form-item>
          <el-form-item prop="region" :label="$t('admin.label-region')" v-show="form.provider == 'MINIO'" label-width="120px">
            <el-input v-model="form.region" placeholder=""></el-input>
          </el-form-item>
          <el-form-item prop="access_key" :label="$t('admin.label-access_key')" label-width="120px">
            <el-input v-model="form.access_key" placeholder="AK"></el-input>
          </el-form-item>
          <el-form-item prop="secret_key" :label="$t('admin.label-secret_key')" label-width="120px">
            <el-input v-model="form.secret_key" placeholder="SK" type="password"></el-input>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="" name="storage">
          <el-form-item prop="title" :label="$t('admin.label-title')" label-width="120px">
            <el-input v-model="form.title" placeholder=""></el-input>
          </el-form-item>
          <el-form-item prop="custom_host" :label="$t('admin.label-custom_host')" label-width="120px">
            <el-input v-model="form.custom_host" placeholder="Endpoint"></el-input>
          </el-form-item>
          <el-form-item prop="root_path" label="" label-width="120px">
            <el-input v-model="form.root_path" placeholder="Bucket"></el-input>
          </el-form-item>
          <el-form-item prop="file_path" label="" label-width="120px">
            <el-input v-model="form.file_path" placeholder=""></el-input>
            <span class="tips" @click="openEnvs"><i class="el-icon-warning"></i></span>
          </el-form-item>
          <!-- <el-form-item prop="idirs" label="" label-width="120px">
            <el-select v-model="form.internal_dirs" multiple filterable allow-create default-first-option style="width: 100%" placeholder="">
              <el-option v-for="item in form.internal_dirs" :key="item" :label="item" :value="item"> </el-option>
            </el-select>
          </el-form-item> -->
        </el-tab-pane>
      </el-tabs>
    </el-form>
    <div class="footer">
      <el-button type="primary" @click="onSubmit">{{ $t("confirm") }}</el-button>
      <el-button @click="visible = false">{{ $t("cancel") }}</el-button>
    </div>

    <!--  -->
    <el-drawer title="" :append-to-body="true" :visible.sync="envDrawerVisible" size="35%">
      <el-table :data="support_envs" size="small" style="padding: 10px 20px">
        <el-table-column property="name" label="" width="150"></el-table-column>
        <el-table-column property="intro" label="" width="150"></el-table-column>
        <el-table-column property="example" label=""></el-table-column>
      </el-table>
    </el-drawer>
  </el-drawer>
</template>

<script>
import yamlLoader from "js-yaml";
import { DialogMixin } from "@/libs/mixin";
export default {
  mixins: [DialogMixin],
  props: {
    form: {
      type: Object,
      default: () => {
        return {
          mode: 1,
          endpoint: "",
          region: "auto",
        };
      },
    },
  },
  data() {
    return {
      providers: [],
      endpoints: [],
      envDrawerVisible: false,
      support_envs: [],
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { pattern: /^[A-Za-z0-9\-]+$/, message: "(-)", trigger: "blur" },
        ],
        provider: [{ required: true, message: "", trigger: "change" }],
        bucket: [{ required: true, message: "", trigger: "blur" }],
        endpoint: [
          { required: true, message: "Endpoint", trigger: "blur" },
          { pattern: /^((?!\d+\.\d+\.\d+\.\d).)+$/, message: "EndpointIP", trigger: "change" },
        ],
        access_key: [{ required: true, message: "AccessKey", trigger: "blur" }],
        secret_key: [{ required: true, message: "SecretKey", trigger: "blur" }],
      },
    };
  },
  computed: {
    editMode() {
      return this.form.id > 0;
    },
  },
  methods: {
    openEnvs() {
      this.envDrawerVisible = true;
      this.$zpan.System.matterPathEnvs().then((ret) => {
        this.support_envs = ret.data;
      });
    },
    refreshEplist(provider) {
      this.form.endpoint = ""; // reset the endpoint
      this.form.region = ""; // reset the region

      provider = provider.toLowerCase();
      this.$axios
        .get(`https://raw.githubusercontent.com/eplist/eplist/main/${provider}.yml`)
        .then((data) => {
          let eplist = yamlLoader.load(data);
          this.endpoints = eplist.endpoints;
        })
        .catch((err) => {
          this.endpoints = [];
        });
    },
    endpointSearch(queryString, cb) {
      if (queryString != "" && this.form.provider.toLowerCase() == "minio") {
        cb([{ value: queryString, region: "auto" }]);
        return;
      }

      var endpoints = this.endpoints.map((item) => {
        return { value: item.endpoint, region: item.region };
      });
      var results = queryString ? endpoints.filter(this.createEndpointFilter(queryString)) : endpoints;
      cb(results);
    },
    createEndpointFilter(queryString) {
      return (ep) => {
        return ep.value.toLowerCase().indexOf(queryString.toLowerCase()) != -1;
      };
    },
    endpointSelect(ep) {
      this.form.region = ep.region;
    },
    onSubmit() {
      this.$refs.form.validate((valid) => {
        if (!valid) {
          return;
        }

        let submit = this.$zpan.Storage.create;
        if (this.form.id) {
          submit = this.$zpan.Storage.update;
        }

        // this.form.idirs = this.form.internal_dirs.join(",");
        submit(this.form).then((ret) => {
          this.close();
          this.finish();
          this.$message({
            type: "success",
            message: this.$t("msg.save-success"),
          });
        });
      });
    },
  },
  mounted() {
    this.$zpan.System.providers().then((ret) => {
      this.providers = ret.data;
    });
  },
};
</script>

<style lang="stylus" >
.el-drawer__body
  overflow: auto;

.footer
  margin-left: 142px;
  margin-top: 25px;
</style>